.content {
  width: 100%;
  height: 100vh;
  position: relative;
  padding-top: 85px;
  overflow: scroll;
  float: right;
  box-sizing: border-box;
}
#more-button {
  float: right;
  width: 50px;
  display: inline-block;
  color: dodgerblue;
  height: 100%;
  font-size: 20px;
  background: transparent;
  margin-right: 10px;
  cursor: pointer;
}
.left-content {
  color: #787878;
  text-align: center;
}
.left-content .left-content-collect {
  margin-top: 20px;
  width: 90%;
  display: inline-block;
}
.left-content .left-content-collect .collect-title {
  text-align: left;
}
.left-content .left-content-collect .collect-title h3 {
  width: 50%;
  text-align: left;
  display: inline-block;
}
.left-content .left-content-collect .collect-title button {
  float: right;
  width: 50px;
  display: inline-block;
  color: dodgerblue;
  height: 100%;
  font-size: 20px;
  background: transparent;
  margin-right: 10px;
  cursor: pointer;
}
.left-content .left-content-collect ul {
  margin-top: 10px;
  width: 100%;
}
.left-content .left-content-collect ul li {
  width: 100%;
}
.left-content .left-content-comment {
  width: 90%;
  display: inline-block;
  margin-top: 50px;
  text-align: left;
}
.left-content .left-content-comment .comment-title {
  width: 100%;
  height: 100%;
}
.left-content .left-content-comment .comment-title h3 {
  width: 50%;
  text-align: left;
  display: inline-block;
}
.left-content .left-content-comment .comment-title button {
  float: right;
  width: 50px;
  display: inline-block;
  color: dodgerblue;
  height: 100%;
  font-size: 20px;
  background: transparent;
  margin-right: 10px;
  cursor: pointer;
}
.left-content .left-content-comment ul {
  width: 100%;
  margin-top: 10px;
}
.left-content .left-content-comment ul li {
  width: 80%;
  margin-bottom: 5px;
}
.left-content .left-content-comment ul li div {
  width: 100%;
  height: 30px;
  color: #212427;
}
.left-content .left-content-comment ul li div p {
  text-indent: 5px;
  margin: 0;
}
.article-item {
  width: 100%;
  min-height: 200px;
  font-size: 0;
}
.article-item .left {
  text-align: center;
  width: 15%;
  display: inline-block;
  vertical-align: top;
  margin-top: 30px;
}
.article-item .right {
  padding: 20px;
  box-sizing: border-box;
  width: 85%;
  height: auto;
  display: inline-block;
  font-size: 16px;
}
.article-item .right .article-info {
  width: 100%;
  height: 40px;
  color: #969696;
}
.article-item .right .article-info h3,
.article-item .right .article-info p,
.article-item .right .article-info span {
  margin: 0;
  display: inline-block;
}
.article-item .right .article-info h3 {
  color: #212427;
  margin-right: 10px;
}
.article-item .right .article-info span {
  float: right;
  margin-right: 5px;
}
.article-item .right .article-text {
  border-radius: 5px;
  width: 100%;
  height: 330px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.15);
  overflow: hidden;
  cursor: pointer;
}
.article-item .right .article-text .picture {
  width: 100%;
  height: 250px;
  background: dodgerblue;
  border-top-left-radius: 5px;
  border-top-right-radius: 5px;
}
.article-item .right .article-text .picture img {
  width: 100%;
  height: 100%;
}
.article-item .right .article-text .title {
  box-sizing: border-box;
  padding: 5px 10px 0 10px;
  width: 100%;
  height: 80px;
  display: flex;
}
.article-item .right .article-text .title .title-left {
  flex: 1;
}
.article-item .right .article-text .title .title-left h2,
.article-item .right .article-text .title .title-left p {
  margin: 0;
  color: #212427;
}
.article-item .right .article-text .title .title-left p {
  margin-top: 10px;
  text-indent: 5px;
  font-size: 12px;
  color: #969696;
}
.article-item .right .article-text .title .title-right {
  flex: auto auto 200px;
  box-sizing: border-box;
  padding-top: 30px;
}
.article-item .right .article-text .title .title-right span {
  color: #969696;
}
.article-item .right .article-script {
  height: 50px;
  width: 100%;
  padding-top: 25px;
  box-sizing: border-box;
}
.article-item .right .article-script .script-left {
  float: left;
  width: 70%;
  height: 100%;
}
.article-item .right .article-script .script-left span {
  color: #dcdcdc;
  margin-right: 10px;
}
.article-item .right .article-script .script-right {
  width: 30%;
  height: 100%;
  float: left;
}
.article-item .right .article-script .script-right button {
  outline: none;
  border: none;
  background: transparent;
  color: #dcdcdc;
  float: right;
  margin-right: 5px;
}
.middle-content {
  background: white;
  border: 1px solid rgba(0, 0, 0, 0.02);
  box-sizing: border-box;
  padding-top: 50px;
}
.middle-content .article {
  width: 100%;
  min-height: 200px;
}
.middle-content .article ul {
  width: 100%;
  min-height: 200px;
}
.middle-content .article ul li {
  width: 100%;
  min-height: 200px;
  box-sizing: border-box;
  border: 1px solid rgba(0, 0, 0, 0.02);
  border-radius: 1px;
}
.collect-item {
  width: 200px;
  height: 60px;
}
.collect-item .collect-item-left {
  width: 80px;
  height: 100%;
  float: left;
  line-height: 80px;
  text-align: center;
}
.collect-item .collect-item-right {
  width: 120px;
  float: left;
}
.collect-item .collect-item-right h3 {
  color: #212427;
  margin: 0 0 5px 0;
  font-size: 14px;
}
.right-content {
  width: 100%;
  text-align: center;
}
.right-content .hot {
  margin-top: 20px;
  display: inline-block;
  width: 90%;
  min-height: 500px;
}
.right-content .hot .hot-title {
  font-size: 0;
  border-bottom: 1px solid rgba(0, 0, 0, 0.07);
}
.right-content .hot .hot-title h3 {
  color: #212427;
  width: 50%;
  height: 40px;
  display: inline-block;
  font-size: 20px;
}
.right-content .hot .hot-title button {
  color: dodgerblue;
  width: 50%;
  outline: none;
  border: none;
  background: transparent;
  font-size: 20px;
}
.right-content .hot ul {
  width: 100%;
  margin-top: 20px;
}
.right-content .hot ul li {
  width: 100%;
}
.hot-content {
  height: 65px;
  padding: 5px;
}
.hot-content .hot-left {
  width: 25%;
  height: 100%;
  float: left;
  text-align: center;
  line-height: 65px;
}
.hot-content .hot-right {
  width: 75%;
  height: 100%;
  float: left;
  text-align: left;
}
.hot-content .hot-right h4 {
  margin: 0;
  color: #212427;
  font-size: 15px;
}
.hot-content .hot-right div {
  color: #969696;
  font-size: 12px;
  margin-top: 5px;
}
.hot-content .hot-right div span {
  float: left;
}
.hot-content .hot-right div span:last-child {
  float: right;
}
