.star-button {
  width: 130px;
  height: 25px;
  display: inline-block;
}
.star-button span {
  padding: 0 3px 0 3px;
  text-align: center;
}
.star-button span i {
  color: #dcdcdc;
}
.star-button span.active i {
  color: sandybrown;
}
.pay-button {
  width: 220px;
  height: 40px;
  border-radius: 20px;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.12);
  overflow: hidden;
  cursor: pointer;
  background: white;
}
.pay-button .button-wrapper > div {
  width: 100%;
  height: 100%;
  text-align: center;
  line-height: 40px;
  font-size: 18px;
  font-weight: bold;
}
.pay-button .button-wrapper {
  transition: 0.5s;
  transition-timing-function: cubic-bezier(0, 0.97, 0.74, 1.16);
  margin-top: -80px;
}
.pay-button .pay-div-pay {
  color: white;
  background: dodgerblue;
}
.pay-button .pay-div-process {
  background: white;
  color: #969696;
}
.pay-button .pay-div-success {
  background: white;
  color: lime;
}
.pay-button.process .button-wrapper {
  margin-top: -40px;
}
.pay-button.success .button-wrapper {
  margin-top: 0;
}
.article-button {
  width: 40px;
  height: 40px;
  background: white;
  border-radius: 50%;
  position: relative;
  transition: 0.3s;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.03);
}
.article-button:hover {
  transform: translateY(2px);
}
.article-button:active {
  transform: translateY(4px);
}
.article-button.active button {
  background: #969696;
}
.article-button.active span {
  background: #333333;
}
.article-button.style2 button {
  background: coral;
}
.article-button.style2 span {
  background: red;
}
.article-button.style3 button {
  background: greenyellow;
}
.article-button.style3 span {
  background: fuchsia;
}
.article-button.style4 button {
  background: coral;
}
.article-button.style4 span {
  background: royalblue;
}
.article-button.style5 button {
  background: blueviolet;
}
.article-button.style5 span {
  background: aqua;
}
.article-button button {
  outline: none;
  border: none;
  border-radius: 50%;
  background: dodgerblue;
  width: 100%;
  height: 100%;
  color: white;
  cursor: pointer;
}
.article-button span {
  position: absolute;
  left: 68%;
  top: -2px;
  text-align: center;
  display: inline-block;
  width: 20px;
  padding: 1px 4px 1px 4px;
  border-radius: 10px;
  font-size: 7px;
  color: white;
  background: burlywood;
  z-index: 4;
}
