.left-right-layout {
  display: flex;
  width: 100%;
  height: 100%;
}
@media screen and (min-width: 100px) and (max-width: 1400px) {
  .left-right-layout {
    width: 1400px;
  }
}
@media screen and (min-height: 100px) and (max-height: 700px) {
  .left-right-layout {
    height: 700px;
  }
}
.left-right-layout .left {
  height: 100%;
  min-width: 60px;
}
.left-right-layout .right {
  flex: 1;
  height: 100%;
  overflow: hidden;
}
.left-middle-right-layout {
  display: flex;
  height: 100%;
  width: 100%;
}
