@keyframes bg-twinkle {
  from {
    background: rgba(0, 0, 0, 0.02);
  }
  to {
    background: transparent;
  }
}
@keyframes el-twinkle {
  from {
    background: rgba(0, 0, 0, 0.2);
  }
  to {
    background: rgba(0, 0, 0, 0.1);
  }
}
#mixin-animation {
  animation: el-twinkle 1s alternate infinite;
}
.loading {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.02);
  animation: bg-twinkle 1s alternate infinite;
}
.loading .head {
  width: 100%;
  height: 90px;
  box-shadow: 0 5px 10px rgba(0, 0, 0, 0.05);
}
.loading .head .left {
  font-size: 0;
  height: 100%;
}
.loading .head .left .picture {
  display: inline-block;
  width: 40%;
  height: 100%;
  position: relative;
}
.loading .head .left .picture::before {
  content: "";
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 50px;
  height: 50px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 50%;
  animation: el-twinkle 1s alternate infinite;
}
.loading .head .left .other {
  display: inline-block;
  width: 50%;
  height: 100%;
  position: relative;
}
.loading .head .left .other::before {
  content: "";
  width: 100px;
  height: 20px;
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  top: 30px;
  left: 10px;
  border-radius: 10px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .head .left .other::after {
  content: "";
  width: 50px;
  height: 15px;
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  border-radius: 10px;
  top: 60px;
  left: 10px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .head .right {
  height: 100%;
  position: relative;
}
.loading .head .right::before {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  width: 200px;
  height: 32px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .head .right::after {
  content: "";
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  left: 210px;
  width: 32px;
  height: 32px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 16px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content {
  position: relative;
  z-index: -1;
  width: 100%;
  height: 80%;
}
.loading .content .content-left .left-item1 {
  width: 80%;
  margin: 50px auto 0 auto;
}
.loading .content .content-left .left-item1 .item1-title {
  width: 130px;
  height: 20px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 10px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-left .left-item1 .item1-content {
  width: 80%;
  height: 400px;
  font-size: 0;
  margin-top: 15px;
}
.loading .content .content-left .left-item1 .item1-content .item1-item {
  display: inline-block;
  width: 60px;
  height: 60px;
  margin: 0 7px 7px 0;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 3px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-left .left-item2 {
  width: 80%;
  margin: 10px auto 0 auto;
}
.loading .content .content-left .left-item2 .left-item2-title {
  background: rgba(0, 0, 0, 0.2);
  width: 200px;
  height: 20px;
  border-radius: 10px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-left .left-item2 .left-item2-content {
  position: relative;
}
.loading .content .content-left .left-item2 .left-item2-content::after {
  position: absolute;
  content: "";
  display: block;
  top: 10px;
  background: rgba(0, 0, 0, 0.2);
  width: 150px;
  height: 10px;
  border-radius: 5px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-left .left-item2 .left-item2-content::before {
  position: absolute;
  top: 40px;
  content: "";
  display: block;
  background: rgba(0, 0, 0, 0.2);
  animation: el-twinkle 1s alternate infinite;
  width: 100px;
  height: 10px;
  border-radius: 5px;
}
.loading .content .content-middle .middle-item2 {
  width: 90%;
  margin: 0 auto;
  overflow: hidden;
}
.loading .content .content-middle .middle-item2 .middle-item2-picture {
  float: left;
  background: rgba(0, 0, 0, 0.2);
  width: 60px;
  height: 60px;
  border-radius: 50%;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-middle .middle-item2 .middle-item2-content {
  float: left;
  width: 83%;
  height: 160px;
  left: 100px;
  margin-left: 10px;
}
.loading .content .content-middle .middle-item2 .middle-item2-content .name {
  width: 150px;
  height: 22px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 11px;
  margin-bottom: 10px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-middle .middle-item2 .middle-item2-content .article-content {
  width: 100%;
  height: 120px;
  border-radius: 10px;
  overflow: hidden;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.12);
}
.loading .content .content-middle .middle-item2 .middle-item2-content .article-content .title {
  margin-top: 30px;
  position: relative;
}
.loading .content .content-middle .middle-item2 .middle-item2-content .article-content .title #mixin {
  content: '';
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  display: block;
  border-radius: 10px;
  left: 5px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-middle .middle-item2 .middle-item2-content .article-content .title::after {
  content: '';
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  display: block;
  border-radius: 10px;
  left: 5px;
  animation: el-twinkle 1s alternate infinite;
  width: 170px;
  height: 20px;
  top: 2px;
}
.loading .content .content-middle .middle-item2 .middle-item2-content .article-content .title::before {
  content: '';
  background: rgba(0, 0, 0, 0.2);
  position: absolute;
  display: block;
  border-radius: 10px;
  left: 5px;
  animation: el-twinkle 1s alternate infinite;
  width: 360px;
  height: 10px;
  top: 40px;
}
.loading .content .content-right {
  position: relative;
  height: 100%;
}
.loading .content .content-right .right-item {
  position: relative;
  background: rgba(0, 0, 0, 0.2);
  top: 20px;
  width: 130px;
  height: 25px;
  border-radius: 13px;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-right .right-item #mixin {
  content: '';
  display: block;
  width: 200px;
  height: 20px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  position: absolute;
  animation: el-twinkle 1s alternate infinite;
}
.loading .content .content-right .right-item::after {
  content: '';
  display: block;
  width: 200px;
  height: 20px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  position: absolute;
  animation: el-twinkle 1s alternate infinite;
  top: 30px;
}
.loading .content .content-right .right-item::before {
  content: '';
  display: block;
  width: 200px;
  height: 20px;
  background: rgba(0, 0, 0, 0.2);
  border-radius: 12px;
  position: absolute;
  animation: el-twinkle 1s alternate infinite;
  top: 70px;
