.tool-button {
  width: 60px;
  height: 60px;
  position: fixed;
  right: 15px;
  bottom: 15px;
  background: transparent;
}
.tool-button.active .main-button {
  background: red;
  transform: rotate(-45deg);
}
.tool-button.active .main-button:hover {
  transform: translateY(3px) rotate(-45deg);
}
.tool-button.active .main-button:active {
  transform: translateY(6px) rotate(-45deg);
}
.tool-button.active #mixin {
  visibility: visible;
  opacity: 1;
  cursor: pointer;
}
.tool-button.active .user-button {
  visibility: visible;
  opacity: 1;
  cursor: pointer;
  transform: translate(5px, -120px) rotate(0);
}
.tool-button.active .user-button:hover {
  transform: translate(5px, -118px) rotate(0);
}
.tool-button.active .user-button:active {
  transform: translate(5px, -116px) rotate(0);
}
.tool-button.active .write-article-button {
  visibility: visible;
  opacity: 1;
  cursor: pointer;
  transform: translate(-85px, -85px) rotate(0);
}
.tool-button.active .write-article-button:hover {
  transform: translate(-85px, -83px) rotate(0);
}
.tool-button.active .write-article-button:active {
  transform: translate(-85px, -81px) rotate(0);
}
.tool-button.active .other-button {
  visibility: visible;
  opacity: 1;
  cursor: pointer;
  transform: translate(-120px, 5px) rotate(0);
}
.tool-button.active .other-button:hover {
  transform: translate(-120px, 7px) rotate(0);
}
.tool-button.active .other-button:active {
  transform: translate(-120px, 9px) rotate(0);
}
.tool-button .main-button {
  width: 100%;
  height: 100%;
  border-radius: 50%;
  background: dodgerblue;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.03);
  transition: 0.3s;
  position: relative;
  z-index: 10;
}
.tool-button .main-button:hover {
  transform: translateY(3px);
}
.tool-button .main-button:active {
  transform: translateY(6px);
}
.tool-button .main-button button {
  background: transparent;
  width: 100%;
  height: 100%;
  outline: none;
  border: none;
  color: white;
  font-size: 35px;
  cursor: pointer;
}
.tool-button #mixin {
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.3s;
  visibility: hidden;
  opacity: 0;
  transform: rotate(-180deg);
  z-index: 1;
  text-align: center;
}
.tool-button #mixin button {
  cursor: pointer;
  width: 100%;
  background: transparent;
  outline: none;
  border: none;
  height: 100%;
  color: white;
  font-size: 20px;
}
.tool-button .user-button {
  width: 100%;
  height: 100%;
  background: transparent;
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.3s;
  visibility: hidden;
  opacity: 0;
  transform: rotate(-180deg);
  z-index: 1;
  text-align: center;
}
.tool-button .user-button button {
  cursor: pointer;
  width: 100%;
  background: transparent;
  outline: none;
  border: none;
  height: 100%;
  color: white;
  font-size: 20px;
}
.tool-button .write-article-button {
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.3s;
  visibility: hidden;
  opacity: 0;
  transform: rotate(-180deg);
  z-index: 1;
  text-align: center;
  background: chartreuse;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.03);
}
.tool-button .write-article-button button {
  cursor: pointer;
  width: 100%;
  background: transparent;
  outline: none;
  border: none;
  height: 100%;
  color: white;
  font-size: 20px;
}
.tool-button .other-button {
  width: 100%;
  height: 100%;
  background: blueviolet;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 0;
  transition: 0.3s;
  visibility: hidden;
  opacity: 0;
  transform: rotate(-180deg);
  z-index: 1;
  text-align: center;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.03);
}
.tool-button .other-button button {
  cursor: pointer;
  width: 100%;
  background: transparent;
  outline: none;
  border: none;
  height: 100%;
  color: white;
  font-size: 20px;
}
