.head-nav {
  width: 100%;
  height: 90px;
  background: white;
  box-shadow: 0 5px 30px rgba(0, 0, 0, 0.12);
  position: absolute;
  z-index: 100;
  box-sizing: border-box;
  padding-right: 10px;
  top: 0;
}
#mixin-center {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}
.user {
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.01);
  border-right: 1px solid rgba(0, 0, 0, 0.02);
}
.user .user-head {
  width: 30%;
  height: 100%;
  padding-top: 15px;
  text-align: center;
  float: left;
}
.user .user-info {
  float: left;
  width: 30%;
  height: 100%;
  text-align: left;
  line-height: 90px;
}
.user .user-info button {
  display: inline-block;
  width: 36px;
  height: 36px;
  font-size: 25px;
  color: #969696;
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.12);
  border-radius: 3px;
  background: white;
  margin-top: 10px;
  outline: none;
  border: none;
}
.user .user-article {
  float: left;
  width: 40%;
  height: 100%;
  position: relative;
}
.user .user-article > div {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  left: 30px;
}
.user .user-article > div h3 {
  color: #787878;
  margin: 0;
}
.user .user-article > div p {
  color: dodgerblue;
  margin: 0;
}
.user .user-article > div p span {
  color: #969696;
}
.nav {
  width: 100%;
  height: 100%;
}
.nav .user-nickname {
  width: 40%;
  height: 100%;
  padding: 20px;
  float: left;
  box-sizing: border-box;
}
.nav .user-nickname h3 {
  margin: 0 0 5px 0;
}
.nav .user-nickname p {
  margin: 0;
}
.nav .classify {
  box-sizing: border-box;
  width: 60%;
  height: 100%;
  float: left;
}
.nav .classify .nav-ul {
  width: 100%;
  height: 100%;
  text-align: right;
  font-size: 0;
}
.nav .classify .nav-ul li {
  display: inline-block;
  width: 90px;
  height: 100%;
}
.search {
  width: 100%;
  height: 100%;
  position: relative;
}
.search > button {
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
  width: 150px;
  height: 32px;
  color: white;
  outline: none;
  background: dodgerblue;
  border: 1px solid blue;
}
.nav-item {
  width: 100%;
  height: 100%;
  position: relative;
  text-align: center;
  font-size: 16px;
  color: #969696;
  cursor: pointer;
  transition: 0.5s;
}
.nav-item:hover,
.nav-item.active {
  background: rgba(0, 0, 0, 0.01);
  color: #212427;
}
.nav-item:hover .bottom-line,
.nav-item.active .bottom-line {
  height: 2px;
}
.nav-item .item-icon {
  font-size: 40px;
  padding-top: 15px;
  width: 100%;
  height: 50%;
}
.nav-item .item-text {
  padding-top: 5px;
  width: 100%;
  height: 50%;
}
.nav-item .bottom-line {
  position: absolute;
  width: 100%;
  height: 0;
  bottom: 0;
  background: gainsboro;
  transition: 0.5s;
}
