.nav {
  width: 70px;
  height: 100vh;
  background: #212427;
  position: relative;
  box-shadow: 5px 0 10px rgba(0, 0, 0, 0.06);
  border-right: rgba(0, 0, 0, 0.5);
}
.nav #mixin-nav {
  width: 70px;
  height: 70px;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  border-top: 1px solid rgba(0, 0, 0, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
}
.nav #mixin-transition {
  transition: 0.5s;
}
.nav .nav-logo {
  width: 70px;
  height: 70px;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  border-top: 1px solid rgba(0, 0, 0, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  color: white;
  border: none;
  display: inline-block;
}
.nav .nav-logo .logo {
  width: 60px;
  height: 60px;
  border: none;
  margin-top: 5px;
}
.nav .nav-home {
  width: 70px;
  height: 70px;
  text-align: center;
  box-sizing: border-box;
  cursor: pointer;
  border-top: 1px solid rgba(0, 0, 0, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  transition: 0.5s;
  line-height: 60px;
  font-size: 25px;
  color: #969696;
  position: relative;
}
.nav .nav-home .left-effect {
  transition: 0.5s;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  width: 3px;
  height: 100%;
  background: dodgerblue;
}
.nav .nav-home:hover {
  background: rgba(255, 255, 255, 0.08);
  color: dodgerblue;
}
.nav .nav-home.active {
  background: rgba(255, 255, 255, 0.08);
  color: dodgerblue;
}
